import dayjs from '@/utils/dayjs.min.js'
import {
	api,
	postajax
} from '@/utils/api.js'
import uCharts from '@/components/u-charts/u-charts.js';
var _self;
var canvaLineA = null;
export default {
	data() {
		return {
			cWidth: '',
			cHeight: '',
			pixelRatio: 1,
			textarea: '',
			startdate: null,
			enddate: null,
			daylist: [],
			nowYear: '',
			nowMonth: '',
			nowWeek: '',
			vNowDate: dayjs(),
			selectTime: '',
			today: dayjs(),
			topiclist: [{
				"topicCode": "463f8e6d3c814958ba3a720c72066550",
				"topicName": "分地方撒",
				"classCode": "38af121b79b547b2b780a01d978869f2",
				"teacherCode": "579",
				"teacherName": "閤",
				"correctPercent": "50.00%"
			}],
			userinfo: {}
		}
	},
	onLoad() {
		_self = this;











		this.cWidth = uni.upx2px(700);
		this.cHeight = uni.upx2px(500);
		this.init();
	},
	onPullDownRefresh() {
		this.init();
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		init() {
			this.getUserinfo();
			this.getdaylist(this.vNowDate);
			this.gettopiclist();
			this.advanceProgress();
			this.selectTime = dayjs().format('YYYY-MM-DD')
			this.getDatePullList();
		},
		prevweek() {
			var vNowDate = this.vNowDate.subtract(7, "day")
			this.getdaylist(vNowDate)
			this.getDatePullList()
		},
		nextweek() {
			var vNowDate = this.vNowDate.add(7, "day")
			this.getdaylist(vNowDate);
			this.getDatePullList()
		},
		getdaylist(dayjs) {
			var vNowDate = dayjs;
			this.enddate = vNowDate.format('YYYY-MM-DD')
			if (vNowDate.day() == 0) {
				vNowDate = vNowDate.subtract(7, "day")
			}
			this.vNowDate = vNowDate;
			this.daylist = [];
			for (var i = 1; i <= 7; i++) {
				var item = {
					allday: vNowDate.day(i).format('YYYY-MM-DD'),
					day: vNowDate.day(i).format('D'),
					isCheck: false
				}

				if (vNowDate.day(i).isAfter(this.today)) {
					item.isdisable = true;
				} else {
					item.isdisable = false;
					if (vNowDate.day(i).isSame(vNowDate)) {
						item.isCheck = true;
					}
				}

				this.daylist.push(item)
			}
			this.startdate = this.daylist[0].allday;
			this.nowYear = this.vNowDate.year();
			this.nowMonth = this.vNowDate.month() + 1;
			this.nowWeek = Math.ceil((this.vNowDate.date() + 6 - (this.vNowDate.day() == 0 ? 7 : this.vNowDate.day())) / 7);
		},
		getDatePullList(){
			postajax(api.getDatePullList, {
				"stuCode": "25dacf6dc11c482587ad84c84ceb9ad7",
				"queryStartTime": "2020-01-02 00:00:0",
				"queryEndTime": "2020-04-20 23:59:59"
			}).then(da => {
				{
					console.log('时间有返回吗')
					console.log(da)
					if (da.code == 0 && da.data && da.data.length > 0) {
						console.log('shijian')
						this.daylist.forEach(item=>{
							var index=da.data.findIndex(subitem=>subitem.title==item)
							if(index>-1){
								item.isdisable=false;
							}else{
								item.isdisable=true;
							}
						})
						console.log(this.daylist)
					}
				}
			})
		},
		gettopiclist() {
			/* 查询主题列表 */
			postajax(api.gettopiclist, {
				"stuCode": "25dacf6dc11c482587ad84c84ceb9ad7",
				"queryDate": "2020-04-02"
			}).then(da => {
				{
					console.log(da)
					if (da.code == 0 && da.data && da.data.length > 0) {
						this.topiclist = da.data;
					}
				}
			})
		},
		advanceProgress() {
			/* 进步趋势 */
			postajax(api.advanceProgress, {
				"queryStartDate": "2020-04-01 00:00:00",
				"queryEndDate": "2020-04-02 23:59:59",
				"classCode": "38af121b79b547b2b780a01d978869f2",
				"stuCode": "25dacf6dc11c482587ad84c84ceb9ad7"
			}).then(da => {
				{
					console.log(da)
					if (da.code == 0 && da.data) {
						// this.topiclist = da.data;
						var trendTimeLineList = da.data.trendTimeLineList;
						var categories = [];
						var studentJoinAccuracy = [];
						var studentCorrectAccuracy = [];
						if (trendTimeLineList && trendTimeLineList.length > 0)
							for (var i = 0; i < trendTimeLineList.length; i++) {
								categories.push(trendTimeLineList[i].date)
								studentJoinAccuracy.push(parseFloat(trendTimeLineList[i].studentCorrectAccuracy.replace(/%/, '')))
								studentCorrectAccuracy.push(parseFloat(trendTimeLineList[i].studentJoinAccuracy.replace(/%/, '')))
							}
						var series = [{
							data: studentCorrectAccuracy,
							name: '综合正确率进步趋势'
						}, {
							data: studentJoinAccuracy,
							name: '参与率进步趋势'
						}]

						this.getServerData(categories, series);
					}
				}
			})
		},
		checkday(index) {
			/* 先判断是否可以点击 */
			if (!this.daylist[index].isdisable) {
				this.daylist.forEach((item, subindex) => {
					if (index == subindex) {
						item.isCheck = true;
						this.selectTime = item.allday;
					} else {
						item.isCheck = false
					}
				})
			}
		},
		getUserinfo() {
			postajax(api.getuserinfo).then(da => {
				{
					console.log(da)
					if (da.code == 0 && da.data && da.data.length > 0) {
						this.userinfo = da.data[0];
						uni.setStorageSync('userinfo', da.data[0])
					}
				}
			})
		},
		getServerData(categories, series) {
			let LineA = {
				categories: [],
				series: []
			};
			//这里我后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
			LineA.categories = categories;
			LineA.series = series;
			_self.showLineA("canvasLineA", LineA);

		},
		showLineA(canvasId, chartData) {
			canvaLineA = new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'line',
				fontSize: 11,
				padding: [15, 20, 0, 15],
				legend: {
					show: true,
					padding: 5,
					lineHeight: 11,
					margin: 0,
				},
				dataLabel: true,
				dataPointShape: true,
				background: '#FFFFFF',
				pixelRatio: _self.pixelRatio,
				categories: chartData.categories,
				series: chartData.series,
				animation: true,
				xAxis: {
					type: 'grid',
					gridColor: '#CCCCCC',
					gridType: 'dash',
					dashLength: 8,
					boundaryGap: 'justify'
				},
				yAxis: {
					gridType: 'dash',
					gridColor: '#CCCCCC',
					dashLength: 8,
					splitNumber: 5,
					format: (val) => {
						return val.toFixed(0) + '%'
					},
					type: 'value',
					max: 100,
					min: 0


				},
				width: _self.cWidth * _self.pixelRatio,
				height: _self.cHeight * _self.pixelRatio,
				extra: {
					line: {
						type: 'curve'
					}
				}
			});

		},
		touchLineA(e) {
			canvaLineA.touchLegend(e);
			canvaLineA.showToolTip(e, {
				format: function(item, category) {
					return category + ' ' + item.name + ':' + item.data
				}
			});
		},
		startDateChange(e) {
			this.startdate = e.detail.value.replace(/-/g, '.')
		},
		endDateChange(e) {
			this.enddate = e.detail.value.replace(/-/g, '.')
		},
		showDetails(obj) {
			console.log(obj)
			uni.navigateTo({
				url: '/pages/classdetails/classdetails?info=' + encodeURIComponent(JSON.stringify(obj))
			})
		}
	},
	filters: {
		filternum(value) {
			let s = value;
			console.log(value)
			if (value && value.length > 0) {
			
				s = value.slice(0, value.length - 1)
			}
			return s
		},
		filterTime(value) {
			let s = value;
			console.log(value)
			if (value && value.length > 0) {

				s = value.slice(10, value.length)
			}
			return s
		}
	}
}
